import CodeTypes from './code-types'

export default function handleError(err, retry, requester) {
  err.requester = requester
  const errorHandler = err.config && err.config.errorHandler
  if(errorHandler) {
    customizeError(err, errorHandler)
    const {
      interceptors,
      fallback,
    } = errorHandler
    const interceptor = interceptors && interceptors[err.code]
    if(interceptor) {
      return interceptor(
        err,
        retry,
        function next() {
          return fallback ? fallback(err) : Promise.resolve()
        }
      ) || Promise.reject(err)
    } else if(fallback) {
      fallback(err)
    }
  }
  throw err
}

function customizeError(err, {
  getCodeFromData,
  getMessageFromData,
  getMessageFromCode
}) {
  if(!err.code) {
    err.code = getCode(err, getCodeFromData)
  }
  err.message = getMessage(err, getMessageFromData, getMessageFromCode) || err.message
  return err
}

function getCode(err, getCodeFromData) {
  let code
  if(err.message === 'Network Error') {
    // adapt to Axios (request.onerror)
    code = CodeTypes.NETWORK_ERROR
  } else if(err.response) {
    const {
      data,
      status,
    } = err.response
    if(data && getCodeFromData) {
      code = getCodeFromData(data)
    }
    if(!isDef(code)) {
      code = status
    }
  }
  return code
}

function getMessage(err, getMessageFromData, getMessageFromCode) {
  const data = err.response && err.response.data
  return (data && getMessageFromData && getMessageFromData(data)) ||
    (getMessageFromCode && getMessageFromCode(err.code))
}

function isDef(val) {
  return val !== undefined && val !== null
}
