<template lang="html">
<div class="menus-wrapper">
  <Menu
    v-show="isShown"
    ref="menu"
    class="menu"
    :open-names="openNames"
    :active-name="$route.fullPath"
    :width="width"
  >
    <SubmenuItem
      v-for="item in menus"
      :key="item.id"
      :item="item"
    />
  </Menu>
  <div
    class="fold-btn"
    :class="isShown ? 'left' : 'right'"
    @click="isShown = !isShown"
  ></div>
</div>
</template>

<script>
import SubmenuItem from './SubmenuItem'

export default {
  components: {
    SubmenuItem
  },
  props: {
    menus: Array,
    width: String,
  },
  data() {
    return {
      isShown: true
    }
  },
  computed: {
    openNames() {
      const activeName = this.$route.fullPath
      function forEach(tree, paths = []) {
        if(tree.children && tree.children.length) {
          paths = paths.concat(tree.id)
          for (let i = 0; i < tree.children.length; i++) {
            const result = forEach(tree.children[i], paths)
            if(result) return result
          }
        } else if(tree.fullPath === activeName) {
          return paths
        }
      }
      for (let i = 0; i < this.menus.length; i++) {
        const result = forEach(this.menus[i])
        if(result) {
          return result
        }
      }
      return []
    }
  },
  watch: {
    openNames() {
      this.$nextTick(() => this.$refs.menu && this.$refs.menu.updateOpened())
    }
  },
}
</script>

<style lang="scss" scoped>
.menus-wrapper {
  position: relative;
  > .fold-btn {
    position: absolute;
    z-index: 99;
    top: 50%;
    left: 100%;
    transform: translateY(-50%);
    margin-left: 3px;
    border: 1px solid rgb(0, 118, 253);
    border-radius: 3px;
    width: 24px;
    text-align: center;
    line-height: 30px;
    font-weight: bold;
    font-family: cursive;
    color: rgb(0, 118, 253);
    background: #fff;
    opacity: 0.3;
    transition: opacity 0.5s;
    &.left {
      &:after {
        content: '<';
      }
    }
    &.right {
      &:after {
        content: '>';
      }
    }
  }
  &:hover {
    .fold-btn {
      opacity: 1;
    }
  }
}

.menu {
  position: relative;
  overflow: auto;
  height: 100%;
  background: #e8edf7;
  user-select: none;
  transition: all 0.4s ease-out;

  .ivu-menu {
    z-index: 8;
  }
  /deep/ {
    .ivu-menu-item:hover,
    .ivu-menu-submenu-title:hover,
    .ivu-menu-item-active:not(.ivu-menu-submenu) {
      background: #bac5d8;
    }
    .ivu-menu-item-active:not(.ivu-menu-submenu) {
      color: #495060;
      font-weight: bold;
    }
    .ivu-menu {
      background: #dbe3ee;
      // background: #bac5d8;
    }
    .ivu-menu-child-item-active:not(.ivu-menu-opened) {
      background: #bac5d8;
    }
  }
}
</style>
