import {
  ApiModule,
} from './lib'
import CodeTypes from './code-types'
import defaults from './defaults'
import kettle from './modules/kettle'
import system from './modules/system'

const ouput = new ApiModule({
  defaults,
  modules: {
    kettle,
    system,
  }
})

ouput.CodeTypes = CodeTypes

export default ouput

export {
  CodeTypes
}
