import Api from './Api'
import mergeConfig from './merge-config'

export default class ApiModule {
  constructor({
    defaults,
    apis,
    modules,
  }) {
    this.$defaults = defaults
    if(apis) {
      this.$apis = this.initApis(apis)
      Object.assign(this, this.$apis)
    }
    if(modules) {
      this.$modules = this.initModules(modules)
      Object.assign(this, this.$modules)
    }
  }
  initApis(apis) {
    const ouput = {}
    const unsolved = {}
    for (let apiName in apis) {
      const apiConfig = apis[apiName]
      if(apiConfig instanceof Array) {
        if(!(apiConfig[0] in apis)) throw new Error(`无法继承接口'${apiConfig[0]}'：接口不存在`)
        unsolved[apiName] = apiConfig
      } else {
        ouput[apiName] = new Api(mergeConfig(this.$defaults, apiConfig), apiName)
      }
    }
    while (Object.keys(unsolved).length) {
      let done = false
      for (let apiName in unsolved) {
        const [parentName, apiConfig] = unsolved[apiName]
        if(parentName in ouput) {
          ouput[apiName] = ouput[parentName].create(apiConfig)
          delete unsolved[apiName]
          done = true
        }
      }
      if(!done) {
        throw new Error(`循环引用：${Object.keys(unsolved)}`)
      }
    }
    return ouput
  }
  initModules(modules) {
    const ouput = {}
    for (let moduleName in modules) {
      const module = modules[moduleName]
      if(!module.independent) {
        module.defaults = mergeConfig(this.$defaults, module.defaults)
      }
      ouput[moduleName] = new ApiModule(module)
    }
    return ouput
  }
}
