/**
 * 处理路由配置项
 * @param {object[]} routes 配置项
 */
export function normalize (routes = []) {
  return (Array.isArray(routes) ? routes : [routes]).map(route => Object.assign({}, route, {
    component: typeof route.component === 'string' ? () => import('../views/' + route.component) : route.component,
    children: normalize(route.children)
  }))
}
