import qs from 'qs'
import {
  Message
} from 'iview'
import {
  ResponseError,
} from './lib'
import CodeTypes from './code-types'

export default {
  method: 'post',
  baseURL: process.env.NODE_ENV !== 'production' ? '/api' : '',
  timeout: 60000,
  headers: {
    'X-Requested-With': 'XMLHttpRequest',
    'Content-Type': 'application/x-www-form-urlencoded'
  },
  errorHandler: {
    fallback(err) {
      Message.error(err.message)
      let message = `请求'${err.config.url}'出错了！`
      const api = err.requester.api
      if(api && api.name) {
        message += `(来自api:${api.name})`
      }
      console.log(message)
    },
    getCodeFromData(data) {
      return data.error && data.error.errorCode
    },
    getMessageFromData(data) {
      return data.error && data.error.errorMessage
    },
    getMessageFromCode(code) {
      return CODE_MESSAGES[code] || CODE_MESSAGES['fallback']
    },
  },
  before(config) {
    // Object.assign(config.headers, store.getters.authHeaders)
    if (config.method && config.method.toLowerCase() === 'post') {
      const contentType = config.headers['Content-Type'] || (config.headers.post && config.headers.post['Content-Type'])
      if (contentType && ~contentType.indexOf('application/x-www-form-urlencoded')) {
        config.data = qs.stringify(config.data, {
          encodeValuesOnly: true,
          skipNulls: true,
          filter: (prefix, val) => {
            if(val === '') return
            return val
          }
        })
      }
    }
    return config
  },
  after(response) {
    const data = response && response.data
    if(data) {
      if(data.success === false) {
        throw new ResponseError(response)
      } else {
        return data.result
      }
    }
    return response
  }
}

const CODE_MESSAGES = {
  fallback: '服务器繁忙，请稍后再试',
  [CodeTypes.NETWORK_ERROR]: '无法连接服务器，请检查您的网络',
  [CodeTypes.TIMEOUT]: '连接超时',
  [CodeTypes.NOT_FOUND]: '未找到接口',
  [CodeTypes.GATEWAY_TIMEOUT]: '服务器网关超时',
}
