<template>
<!-- eslint-disable -->
<Submenu
  v-if="item.children && item.children.length"
  :name="item.id"
>
  <template slot="title">
    <Icon
      class="icon"
      :type="item.icon"
      size="16"
    />
    {{item.label}}
  </template>
  <SubmenuItem
    v-for="subitem in item.children"
    :key="subitem.id"
    :item="subitem"
  />
</Submenu>
<MenuItem
  v-else
  :name="item.fullPath"
  :to="item.fullPath"
>
  <Icon
    class="icon"
    :type="item.icon"
    size="16"
  />
  {{item.label}}
</MenuItem>
</template>

<script>
export default {
  name: 'SubmenuItem',
  props: {
    item: Object
  },
}
</script>
