import assignWith from 'lodash/assignWith'
import isObject from 'lodash/isObject'

const DEEP_MERGE_PROPERTIES = ['headers', 'auth', 'proxy', 'params', 'data', 'errorHandler']

export default function mergeConfig(...args) {
  return assignWith({}, ...args, function mergeDeep(objValue, srcValue, key) {
    if(~DEEP_MERGE_PROPERTIES.indexOf(key) && isObject(srcValue) && isObject(objValue)) {
      return Object.assign({}, objValue, srcValue)
    }
  })
}
