import mergeConfig from './merge-config'
import Requester from './Requester'

export default class Api {
  /**
   * @param {Object} config
   * @param {String} [name]
   */
  constructor(config, name) {
    this.config = config
    this.name = name
  }
  /**
   * 以当前api为基础创建新api
   * @param  {Object} config
   * @param {String} [name]
   * @return {Api}
   */
  create(config, name) {
    return new Api(mergeConfig(this.config, config), name)
  }
  /**
   * 以当前api为基础创建新requester
   * @param  {Object} config
   * @return {Requester}
   */
  open(config) {
    return new Requester(mergeConfig(this.config, config), this)
  }
  // shortcut
  send(data) {
    return this.open().send(data)
  }
}
