import mergeConfig from './merge-config'
import normalizeConfig from './normalize-config'
import handleError from './handle-error'
import request from './request'
import { Cancel } from 'axios'

/**
 * 增强Requester
 * 1.{object} config.errorHandler 错误处理管理
 * 2.{array} config.before & config.after
 * 3.{Brake} config.cancelToken无效，改为使用config.brake（SPA里进入路由时可以新建一个Brake供创建Requester的闯入，离开路由的时候执行Brake）
 * 4.{Manager} config.manager 请求池管理
 */
export default class Requester {
  /**
   * unique、manager应该放在Api
   *
   * @param {object} [config.errorHandler] 错误拦截配置，响应本地网络异常、超时和后端自定义异常
   * @param {function(responseData): (number|string)} [config.errorHandler.getCodeFromData] 获取后端自定义错误码；如果该值为空，错误码等于状态码
   * @param {function(responseData): string} [config.errorHandler.getMessageFromData] 获取后端自定义错误信息
   * @param {function(errorCode): string} [config.errorHandler.getMessageFromCode] 当无法获取后端自定义错误信息，利用code获得前端自定义错误信息
   * @param {function(error)} [config.errorHandler.globalHandler] 通用错误处理
   * @param {object.<errorCode, callback: (function(error, requestFn, next): (Promise|undefined))>} [config.errorHandler.interceptorMapping] 定向错误拦器截映射表，匹配时不会执行通用错误处理，可通过执行next参数调用通用错误处理；如果有返回值会替换catch的返回值
   *
   * @param {object|boolean} [config.unique] 控制如何处理重复请求，值为true时，作为{ strict: false, action: 'prevent' }的简写
   * @param {boolean} [config.unique.strict=false] 为false时如果请求的api一致算作重复，为true时还要求请求的url一致
   * @param {string} [config.unique.action=prevent] 可选值：[prevent|override]。'prevent'：阻止新请求；'override'：取消前一个请求
   *
   * @param {RequesterManager} [config.manager]
   */
  constructor(config, api) {
    this.config = config
    this.api = api
    this.done = false
    this.aborted = false
    this.promise = new Promise(resolve => {
      this.resolvePromise = resolve
    })
    if(this.config.brake) {
      this.config.brake.push(this.abort.bind(this))
    }
  }
  clone() {
    return new Requester(mergeConfig(this.config), this.api)
  }
  abort(message) {
    if(!this.aborted) {
      this.aborted = true
      this.abortReason = new Cancel(message)
      if(!this.done) {
        this.resolvePromise(Promise.reject(this.abortReason))
      } else {
        this.abortRequest && this.abortRequest(message)
      }
    }
  }
  send(data = {}) {
    if(!this.done && !this.aborted) {
      this.done = true
      const config = normalizeConfig(mergeConfig(this.config, { data }))
      const {
        manager,
        before,
        after,
      } = config

      let prelude = Promise.resolve(config)
      if(before) {
        prelude = prelude.then(before).then(newConfig => newConfig || config)
        // prelude = prelude.then(config => before(config) || config)
      }
      if(manager) {
        prelude = prelude.then(config => Promise.resolve(manager.register(this)).then(() => config))
      }

      const execute = () => {
        let result = prelude.then(dispatchRequest.bind(this))
        if(after) {
          result = result.then(after)
        }
        return result.catch(err => handleError(err, execute, this))
      }

      this.resolvePromise(execute())
    }
    return this.promise
  }
}

function dispatchRequest(config) {
  if(this.abortReason) {
    throw this.abortReason
  }
  const {
    value,
    abort
  } = request(config)
  this.abortRequest = abort
  return value
}
